const ADD_MESSAGE = 'samuraiNetwork/dialogsReducer/ADD-MESSAGE'

let initialState = {
    contactsData: [
        {id: 1, name: "Andrew"},
        {id: 2, name: "Dmitry"},
        {id: 3, name: "Sasha"},
        {id: 4, name: "Sveta"},
        {id: 5, name: "Valera"},
        {id: 6, name: "Viktor"},
        {id: 7, name: "Alex"},
        {id: 8, name: "Misha"},
        {id: 9, name: "Vadim"},
        {id: 10, name: "Yan"}
    ],
    messagesData: [
        {
            id: 1,
            name: "Dima",
            message: "I am a normal popover and i can have everything",
            avatar: "data:image/jpeg;base64,"
        },
        {
            id: 2,
            name: "Me",
            message: "I am a normal popover #2 and i can have everything",
            avatar: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSJbsBSPWYZK6exsVL86MJuEIOxkWAdAYdxiOCjBCDXq3u2f9RkAw",
        },
        {
            id: 3,
            name: "Dima",
            message: "I am a normal popover #3 and i can have everything",
            avatar: "data:image/jpeg;base64,"
        },
        {
            id: 4,
            name: "Me",
            message: "I am a normal popover #4 and i can have everything",
            avatar: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSJbsBSPWYZK6exsVL86MJuEIOxkWAdAYdxiOCjBCDXq3u2f9RkAw",
        }
    ],
}

const dialogsReducer = (state = initialState, action) => {

    switch (action.type) {
        case ADD_MESSAGE :
            let newMessage = {
                id: state.messagesData.length + 1,
                name: "Me",
                message: action.newMessageText,
                avatar: "https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcSJbsBSPWYZK6exsVL86MJuEIOxkWAdAYdxiOCjBCDXq3u2f9RkAw"
            }
            return {
                ...state,
                messagesData: [...state.messagesData, newMessage],
            }

        default:
            return state;
    }
}

export const addMessage = (newMessageText) => ({type: ADD_MESSAGE, newMessageText})

export default dialogsReducer